import uniLoadMore from '@/colorui/components/uni-load-more.vue';
import misEnum from '../../common/mis-enum.js';
export default {
	components: {
		uniLoadMore
	},
	onLoad(option) {
		if (option.status) {
			var index = 0;
			misEnum.RepairRecordEnumMap.forEach((value, key, map) => {
				if (key == option.status) {
					this.TabCur = index;
					return;
				}
				index++;
			});
			this.recordStatus = option.status;
		} else {
			this.recordStatus = Array.from(misEnum.RepairRecordEnumMap.keys())[0];
		}
	},
	onShow() {
		this.recordStatusTab(this.TabCur);
	},
	data() {
		return {
			page: 1,
			pageSize: 10,
			recordStatus: "",
			status: 'more',
			StatusEnumMap: misEnum.RepairRecordEnumMap,
			TabCur: 0,
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			},
			records: []
		}
	},
	onReachBottom() {
		if (this.status !== "noMore") {
			this.status = 'more';
			this.loadData();
		}
	},
	methods: {
		loadData() {
			this.status = 'loading';
			global.$http.post('/car/repair/list', {
				params: {
					status: this.recordStatus,
					sp_type: 0,
					page: this.page,
					pageSize: this.pageSize
				},
			}).then(res => {
				if (res.status === "0") {
					if (res.data.totlePage <= this.page) {
						this.status = 'noMore';
					} else {
						this.status = "more";
					}
					if (this.page === 1) {
						this.records = res.data.list;
					} else {
						res.data.list.forEach(c => {
							this.records.push(c);
						});
					}
					this.page++;
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		getStatusStr: function(status) {
			return misEnum.RepairRecordEnumMap.get(status);
		},
		recordStatusTab: function(index) {
			this.TabCur = index;
			this.recordStatus = Array.from(this.StatusEnumMap.keys())[index];
			this.page = 1;
			this.records = [];
			this.loadData();
		},
		recordDetail: function(record) {
			uni.navigateTo({
				url: "../work/repairRecordDetail?id=" + record.id,
			});
		}
	}
}
